import axios from "axios";
import {useForm} from "react-hook-form";
import {ErrorMessage} from "@hookform/error-message"
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../utils/Common";
import {useState} from "react";

const Documents = ({agent, ReadAgentDetails, handleClose}) => {
  const {
    register,
    handleSubmit,
    formState: {errors},
    setError,
    clearErrors,
    reset
  } = useForm();

  const onSubmit = async (data) => {
    try {
      // console.log(agent)
      data["UserId"] = agent?.UserId;
      let formData = new FormData();
      formData.append("data", JSON.stringify(data));
      formData.append("UploadDocument", data.UploadDocument[0])

      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/update-agent-document";

      let res = await axios.post(url, formData, {
        headers: {'Authorization': `Bearer ${token}`}
      });

      if (res.status === 200) {
        // console.log(res.data);

        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          reset();
          handleClose();
          successToast(res.data.message);
          ReadAgentDetails(agent?.AspNetUserId);
        }
      }
    } catch (e) {
      errorToast(e.message);
    }
  }

  const [documentType, setDocumentType] = useState("")

  const checkDocumentType = (document_type) => {
    if (!document_type) {
      setDocumentType("");
      setError("DocumentType", {
        type: "required",
        message: "This field is required.",
      })
    } else {
      clearErrors("DocumentType");
      setDocumentType(document_type);
    }
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="mb-3">
          <label htmlFor="DocumentType">Document Type</label>
          <select name="DocumentType" id="DocumentType" className="form-select"
                  {...register("DocumentType", {required: 'This field is required.'})}
                  onChange={(e => checkDocumentType(e.target.value))}>
            <option value="">Please Select</option>
            <option value="PanCard">Pan Card</option>
            <option value="AddressProof">Address Proof</option>
            <option value="AddressProof2">Address Proof 2</option>
            <option value="TDSCertificate">TDS Certificate</option>
            <option value="GSTCertificate">GST Certificate</option>
          </select>

          <ErrorMessage errors={errors} name="DocumentType"
                        render={({message}) => <p className="text-danger">{message}</p>}/>
        </div>

        {/* TDSCertificate */}
        {documentType === "TDSCertificate" && <div className="mb-3">
          <label htmlFor="DocumentNumber">TDS Percentage</label>
          <input type="number" id="DocumentNumber" name="DocumentNumber" className="form-control" min={0}
                 {...register('DocumentNumber', {required: 'This field is required.'})}/>

          <ErrorMessage errors={errors} name="DocumentNumber"
                        render={({message}) => <p className="text-danger">{message}</p>}/>
        </div>}

        {/* PanCard / GSTCertificate */}
        {(documentType === "PanCard" || documentType === "GSTCertificate") && <div className="mb-3">
          <label htmlFor="DocumentNumber">Document Number</label>
          <input type="text" id="DocumentNumber" name="DocumentNumber" className="form-control"
                 {...register('DocumentNumber', {required: 'This field is required.'})}/>

          <ErrorMessage errors={errors} name="DocumentNumber"
                        render={({message}) => <p className="text-danger">{message}</p>}/>
        </div>}

        {documentType === "AddressProof" && <label htmlFor="AddressProof">Address Proof Type</label>}
        {documentType === "AddressProof2" && <label htmlFor="AddressProof">Second Address Proof Type</label>}

        {/* AddressProof / AddressProof2 */}
        {(documentType === "AddressProof" || documentType === "AddressProof2") && <div className="mb-3">
          <select name="AddressProof" id="AddressProof" className="form-select"
                  {...register('AddressProof', {required: 'This field is required.'})}>
            <option value="">Please Select</option>
            <option value="Aadhaar Number">Aadhaar Number</option>
            <option value="Passport">Passport</option>
            <option value="Driving License">Driving License</option>
            <option value="Voter Id">Voter Id</option>
            <option value="Ration Card with Address">Ration Card with Address</option>
            <option value="Vehicle Registration Certificate">
              Vehicle Registration Certificate
            </option>
            <option value="Credit Card Statement">Credit Card Statement</option>
            <option value="Utility Bill - Electricity, Telephone, Mobile">
              Utility Bill - Electricity, Telephone, Mobile
            </option>
            <option value="Shop Establishment or Trade License">
              Shop Establishment or Trade License
            </option>
            <option value="Utility Bill of office Address - Electricity, Telephone, Mobile">
              Utility Bill of office Address - Electricity, Telephone, Mobile
            </option>
            <option value="Registered Sale / Lease Agreement / Rent Agreement with Notary">
              Registered Sale / Lease Agreement / Rent Agreement with Notary
            </option>
            <option value="Latest Bank Statement -Not more than 3 month old">
              Latest Bank Statement -Not more than 3 month old
            </option>
            <option value="Udyog Aadhaar">Udyog Aadhaar</option>
            <option value="Certificate of Incorporation">Certificate of Incorporation</option>
            <option value="Partnership Deed with office location">Partnership Deed with office location</option>
          </select>

          <ErrorMessage errors={errors} name="AddressProof"
                        render={({message}) => <p className="text-danger">{message}</p>}/>
        </div>}

        <div className="mb-3">
          <label htmlFor="UploadDocument">Upload Document</label>
          <input type="file" id="UploadDocument" name="UploadDocument" className="form-control"
                 {...register('UploadDocument', {required: 'This field is required.'})}/>

          <ErrorMessage errors={errors} name="UploadDocument"
                        render={({message}) => <p className="text-danger">{message}</p>}/>
        </div>

        <div className="col-12 text-center mt-2">
          <button className="btn bg-btn w-100 text-white">Update</button>
        </div>
      </form>
    </>
  )
}
export default Documents;
