import {useLocation, useNavigate} from "react-router-dom";
import React, {useEffect, useState} from "react";
import axios from "axios";
import Modal from "react-bootstrap/Modal";
import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import AuthorizedSignatoryDetails from "../../../components/forms/AuthorizedSignatoryDetails";
import Documents from "../../../components/forms/Documents";

const AgentDetails = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [agent, setAgent] = useState(null);

  async function ReadAgentDetails(UserId) {
    try {
      // console.log("UserId ", UserId)
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/getCompanyDetailByUserId/" + UserId

      let res = await axios.post(url, {}, {
        headers: {'Authorization': `Bearer ${token}`}
      });

      // console.log("Agent Info. Fetched");
      // console.log(res.data.recordset[0]);
      if (res.status === 200 && !res.data.error) {
        // console.log(res.data.recordset[0]);
        setAgent(res.data.recordset[0]);
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  useEffect(() => {
    window.scrollTo(0, 0);

    if (!location.state) {
      navigate("/manage-agents");
    } else {
      // console.log(location.state)
      ReadAgentDetails(location.state).then();
    }
  }, [location]);

  function FormatDate(originalDate) {
    const date = new Date(originalDate);
    const formattedDate = date.toISOString().split('T')[0];
    const formattedTime = date.toTimeString().split(' ')[0].slice(0, 5);
    const result = `${formattedDate} ${formattedTime}`;
    return result;
  }

  async function ActivateAgent(UserId, AspNetUserId) {
    if (confirm('Are you sure?')) {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/activate-agent"

      let res = await axios.post(url, {Id: UserId[0]}, {
        headers: {'Authorization': `Bearer ${token}`}
      });
      // console.log(res.data);

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message);
      }
      if (res.status === 200 && !res.data.error) {
        successToast(res.data.message);
        ReadAgentDetails(AspNetUserId).then();
      }
    }
  }

  async function InactiveAgent(UserId, AspNetUserId) {
    if (confirm('Are you sure?')) {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/deactivate-agent"

      let res = await axios.post(url, {Id: UserId[0]}, {
        headers: {'Authorization': `Bearer ${token}`}
      });
      // console.log(res);
      // console.log(res.data);

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message);
      }
      if (res.status === 200 && !res.data.error) {
        successToast(res.data.message);
        ReadAgentDetails(AspNetUserId).then();
      }
    }
  }

  /* Edit Modal */
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  /* Edit Modal - Documents */
  const [showDocuments, setShowDocuments] = useState(false);
  const handleCloseDocuments = () => setShowDocuments(false);
  const handleShowDocuments = () => setShowDocuments(true);

  return (
    <>
      {/* Authorized Signatory Details */}
      <CCard className="mb-4">
        <CCardHeader className="">
          <div className="row">
            <div className="col-6">
              <h4 className="mb-0 primary-color">Authorized Signatory Details</h4>
            </div>
            <div className="col-6 text-end">
              <button onClick={handleShow} className="btn btn-success px-3 btn-sm text-white">
                <i className="fa fa-edit"></i> Edit Details
              </button>
            </div>
          </div>
        </CCardHeader>
        <CCardBody>
          <div className="row">
            <div className="mb-3 col-md-4">
              <p>User Status: {!agent ? '' : agent?.IsActive ?
                <span className="badge bg-success text-white">Active</span> :
                <span className="badge bg-danger text-white">InActive</span>}
              </p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Registration Date: <b>{agent?.CreatedOn && FormatDate(agent?.CreatedOn)}</b></p>
            </div>

            <div className="mb-3 col-md-4 text-end">
              {!agent ? '' : agent?.IsActive === false ?
                <button onClick={() => ActivateAgent(agent?.UserId, agent?.AspNetUserId)}
                        className="btn btn-success text-white px-4">
                  Activate
                </button> :
                <button onClick={() => InactiveAgent(agent?.UserId, agent?.AspNetUserId)}
                        className="btn btn-danger text-white px-3">
                  <i className="fas fa-times"></i> Inactive
                </button>
              }
            </div>

            <div className="mb-3 col-md-4">
              <p>First Name: <b>{agent?.FirstName}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Last Name: <b>{agent?.LastName}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Email Id: <b>{agent?.Email}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Mobile Number: <b>{agent?.PhoneNumber}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Whatsapp Number: <b>{agent?.WhatsappNumber}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Alternate Number: <b>{agent?.AlternateNumber}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Address Line 1: <b>{agent?.Address}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>City: <b>{agent?.City}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>State: <b>{agent?.State}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p className="mb-md-0">Country: <b>{agent?.Country}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p className="mb-0 mb-md-0">Postal Code: <b>{agent?.PinCode}</b></p>
            </div>
          </div>
        </CCardBody>
      </CCard>

      <CCard className="mb-4">
        <CCardHeader className="">
          <h4 className="mb-0 primary-color">Access Details</h4>
        </CCardHeader>
        <CCardBody>
          <div className="row">
            <div className="mb-3 col-md-4">
              <p>User Name: <b>{agent?.Email}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Agency Id: <b>{agent?.CompanyId}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Group Type: <b>{agent?.GroupType}</b></p>
            </div>
          </div>
        </CCardBody>
      </CCard>

      <CCard className="mb-4">
        <CCardHeader className="">
          <h4 className="mb-0 primary-color">Agency Details</h4>
        </CCardHeader>
        <CCardBody>
          <div className="row">
            <div className="mb-3 col-md-4">
              <p>Agency Name: <b>{agent?.CompanyName}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Nature of Business: <b>{agent?.NatureOfBusiness}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Business Type: <b>{agent?.BusinessType}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>PAN Number: <b>{agent?.PANNumber}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>TDS Exemption: <b>{agent?.TDSExemption}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>TDS (% for exemption): <b>{agent?.TDSPercentage}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>IATA No.: <b>{agent?.IATACode}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Referenced By: <b>{agent?.ReferencedBy}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Name or ID:</p>
            </div>

            <div className="mb-3 col-md-4">
              <p>GST Number: <b>{agent?.GSTNumber}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Sales Executive Name:</p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Payment Type: <b>{agent?.PaymentType}</b></p>
            </div>
          </div>
        </CCardBody>
      </CCard>

      {/* Account Info */}
      <CCard className="mb-4">
        <CCardHeader className="">
          <h4 className="mb-0 primary-color">Account Info</h4>
        </CCardHeader>
        <CCardBody>
          <div className="row">
            <div className="mb-3 mb-md-0 col-md-4">
              <p>Total Balance: <b>{agent?.TotalBalance}</b></p>
            </div>

            <div className="mb-3 mb-md-0 col-md-4">
              <p>Credit Limit: <b>{agent?.TotalCreditLimit}</b></p>
            </div>

            <div className="mb-0 col-md-4">
              <p>Due Amount: <b>{agent?.DueAmount}</b></p>
            </div>
          </div>
        </CCardBody>
      </CCard>

      {/* Documents */}
      <CCard className="mb-4">
        <CCardHeader className="">
          <div className="row">
            <div className="col-6">
              <h4 className="mb-0 primary-color">Documents</h4>
            </div>
            <div className="col-6 text-end">
              <button onClick={handleShowDocuments} className="btn btn-success px-3 btn-sm text-white">
                <i className="fa fa-edit"></i> Update Document
              </button>
            </div>
          </div>
        </CCardHeader>
        <CCardBody>
          <div className="row">
            <div className="mb-3 col-md-4">
              <p>Agency Logo: {agent?.AgencyLogo ?
                <a href={ipAddress + "uploads/" + agent?.AgencyLogo}
                   target="_blank" className="file-link">View&nbsp;/&nbsp;Download</a> :
                <b>Not Available</b>}
              </p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Profile Picture: {agent?.ProfilePicture ?
                <a href={ipAddress + "uploads/" + agent?.ProfilePicture}
                   target="_blank">View&nbsp;/&nbsp;Download</a> :
                <b>Not Available</b>}
              </p>
            </div>

            <div className="mb-3 col-md-4">
              <p>PAN: {agent?.PANNumberDocument ?
                <a href={ipAddress + "uploads/" + agent?.PANNumberDocument}
                   target="_blank">View&nbsp;/&nbsp;Download</a>
                :
                <b>Not Available</b>}
              </p>
            </div>

            <div className="mb-3 col-md-4">
              <p>TDS Certificate: {agent?.TDSCertificate ?
                <a href={ipAddress + "uploads/" + agent?.TDSCertificate}
                   target="_blank">View&nbsp;/&nbsp;Download</a>
                :
                <b>Not Available</b>}</p>
            </div>

            <div className="mb-3 col-md-4">
              <p>GST Certificate: {agent?.GSTCertificate ?
                <a href={ipAddress + "uploads/" + agent?.GSTCertificate}
                   target="_blank">View&nbsp;/&nbsp;Download</a> :
                <b>Not Available</b>}
              </p>
            </div>

            <div className="mb-3 col-md-4"></div>

            <div className="mb-3 col-md-4">
              <p>Address Document Type: <b>{agent?.AddressProof}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Address Proof Type: {agent?.AddressProofDocument ?
                <a href={ipAddress + "uploads/" + agent?.AddressProofDocument}
                   target="_blank">View&nbsp;/&nbsp;Download</a> :
                <b>Not Available</b>}
              </p>
            </div>

            <div className="mb-3 col-md-4"></div>

            <div className="mb-3 col-md-4">
              <p>Second Address Document
                Type: <b>{agent?.SecondAddressProof ? agent?.SecondAddressProof : 'Not Available'}</b></p>
            </div>

            <div className="mb-3 col-md-4">
              <p>Second Address Proof Type: {agent?.SecondAddressProofDocument ?
                <a href={ipAddress + "uploads/" + agent?.SecondAddressProofDocument}
                   target="_blank">View&nbsp;/&nbsp;Download</a> :
                <b>Not Available</b>}</p>
            </div>
          </div>
        </CCardBody>
      </CCard>

      {/* Modal - Authorized Signatory Details */}
      <Modal show={show} onHide={handleClose} centered fullscreen size="lg">
        <Modal.Header closeButton>
          <h4>Update Details</h4>
        </Modal.Header>
        <Modal.Body>
          <AuthorizedSignatoryDetails agent={agent} ReadAgentDetails={ReadAgentDetails} handleClose={handleClose}/>
        </Modal.Body>
      </Modal>

      {/* Modal - Update Document */}
      <Modal show={showDocuments} onHide={handleCloseDocuments} centered>
        <Modal.Header closeButton>
          <h4 className="mb-0 primary-color">Update Document</h4>
        </Modal.Header>
        <Modal.Body>
          <Documents agent={agent} ReadAgentDetails={ReadAgentDetails} handleClose={handleCloseDocuments}/>
        </Modal.Body>
      </Modal>
    </>
  )
}
export default AgentDetails;
